import React from 'react';
import {Pagination as MuiPagination} from '@material-ui/lab'

function Pagination(){

  const onChange=(page)=>{
    console.log(page);
  }
return (
      <div>
          <MuiPagination  count={10} onChange={(e,page)=>{
              onChange(page);
          }}/>
      </div>
      )
}
export default Pagination