import React from 'react';
import {Drawer as MuiDrawer} from '@material-ui/core';

function Drawer(props){

    const onClose=()=>{
        if(props.onClose){
            props.onClose(false);
        }
      
    }
return (
      <div>
          <MuiDrawer open={props.open} onClose={()=>onClose()}>
              {props.children}
          </MuiDrawer>
      </div>
      )
}
export default Drawer