import React from 'react';
import Table from './component/table';
import * as localActions from './service/locale/action';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
// import {Select,MenuItem} from '@material-ui/core';
import ControlComponent from './component/controlComponent/index'
import { Button } from '@material-ui/core';
import Search from './component/search/index';
import Dialog from './component/dialog/index';
import Progress from './component/prgress/index';
import Drawer from './component/drawer/index';
import Pagination from './component/pagination/index';
import * as configAction from './service/config/configAction'
import Pdf from './component/pdf/index'

function TestComponent(props) {
    console.log(window.MockData);
    const [local, setLoate] = React.useState(null);
    console.log(process.env.NODE_ENV)
    const testUrl=process.env.NODE_ENV==="development"?"/music/Lecture1.pdf":"http://116.62.42.204/music/Lecture1.pdf";
    const [index, setIndex] = React.useState(100);
    const [open, setOpen] = React.useState(false);
    const [two, setOpenTwo] = React.useState(false);
    const [drawer, setDrawer] = React.useState(false);
     const [url,setUrl] = React.useState(null);
     

    return (
        <div>
            <Button onClick={() => {
                props.changeLayout(props.config.pc.menuLayout === "header" ? 'aside' : 'header')
                //setDrawer(!drawer);
                //setOpen(!open);
            }}>test</Button>
            <Button onClick={() => {
               setUrl(url?'':testUrl);
            }}>pdf</Button>
            <Pdf url={url}></Pdf>
            <Pagination></Pagination>
            <Progress type="circle" />
            <Drawer
                open={drawer}
                onClose={() => {
                    setDrawer(false);
                }}
            >
            </Drawer>
            <Dialog
                title="aaaa"

                // drag={true}
                open={open}
                onClose={(test) => {
                    props.changeLayout("header")
                    // setOpen(test);
                }}
            >
                <>
                    <Button onClick={() => {
                        setOpenTwo(true)
                    }}>two</Button>
                    <Dialog title="two" open={two} onClose={(open) => {
                        setOpenTwo(open)
                    }}>
                        <div>two</div>
                    </Dialog>
                </>
            </Dialog>
            {/* {[...Array(10000)].fill(true).map((item,i)=>{
              return (
              <div key={`${i}`}>{i}</div>
              )
          })} */}
            {/* <ControlComponent type="integer"/>
          <ControlComponent type="double"/> */}
            {/* <Search data={window.MockData.searchData}/> */}
            {/* <ControlComponent type="select" options={[{label:'aa',value:1}]}/> */}
            {/* <Button onClick={()=>{
              setIndex(index+100);
          }}>
              {index}
          </Button> */}
            {/* <Table columns={window.MockData.table.columns} selectType="checkbox" data={window.MockData.table.data(index)}
          selectRow={(params)=>{
             // console.log(params);
          }}
          /> */}

        </div>
    )
}

export default connect((state) => ({ ...state }), { ...localActions, ...configAction })(withRouter(TestComponent));