import React from 'react';
import { Popper as MuiPopover, List, ListItem, ListItemText, Paper } from '@material-ui/core';

function Popover(props) {
    const { options } = props;
    const ref = React.createRef();
    const [anchor, setAnchor] = React.useState(false);
    const selectItem = (item) => {
        if (props.selectItem) {
            props.selectItem(item);
        }
    }
    return (
        <div ref={ref} onMouseEnter={() => {
            if (props.hover) {
                if (props.open) {
                    setAnchor(ref.current);
                }

            }

        }}
            onMouseLeave={() => {
                setAnchor(null);
            }}
        >
            <div>
                {props.children}
            </div>
            <MuiPopover
                placement={props.placement}
                style={{
                    zIndex: 9999999
                }}
                onClose={() => {
                    setAnchor(false);
                }}

                anchorEl={anchor}
                open={anchor instanceof Object ? true : false}
            >
                <Paper style={{ position: 'relative', bottom: 1, width: 200 }}>
                    {options ?
                        <List component="nav">
                            {!Array.isArray(options) || options.map((item, index) => {
                                return (
                                    <div key={`${index}`}>
                                        <ListItem onClick={() => selectItem(item)} button>
                                            <ListItemText primary={item instanceof Object ? item.label : item}></ListItemText>
                                        </ListItem>

                                    </div>
                                )
                            })}
                        </List> : (<div>{props.child}</div>)}
                </Paper>
            </MuiPopover>
        </div>
    )
}
export default Popover;