import React from 'react';
import {useForm} from 'react-hook-form';
import Controls  from  '../controlComponent/index';
import {Grid} from '@material-ui/core';


function Search(props){
    const {register} =useForm();
    const [newData,setData]=React.useState([]);
    React.useEffect(()=>{
       let arr=[];
       [...Array(4)].fill(true).forEach((item,i)=>{
           let cache=props.data.slice(i*4,4*(i+1));
           if(cache.length!==0){
            console.log(cache);
               arr.push(cache);
           }
       });
     
       setData(arr);

    },[props.data])
    return (
        <div>
            {newData.map((item,i)=>{
                return(
                   <Grid  key={`${i}`} container>
                          {
                              item.map((child,k)=>{
                                  console.log(child);
                                  return(
                                    <Grid key={`${k}`} xs item>
                                            <Controls type={child.type} inputRef={register} name={child.name} {...child}/> 
                                    </Grid>
                                  )
                              })
                          }
                   </Grid>
                )
            })}
        </div>
    )
}
export default Search;