import React from 'react';
import mainStyle from '../../styles/index'
import clsx from 'clsx';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import { IconButton,Select,MenuItem} from '@material-ui/core';
import Menu from '../../component/menu/index';

function MenuLayout(props){
    const classes=mainStyle.mainStyle();
    if(props.layout==="aside"){
        return (
            <Drawer
                  variant="permanent"
                  className={
                      clsx(classes.drawer,{
                          [classes.drawerOpen]:props.open,
                          [classes.drawerClose]:!props.open
                      })
                  }
                  classes={
                      {
                          paper:clsx({
                              [classes.drawerOpen]:props.open,
                              [classes.drawerClose]:!props.open
                          })
                      }
                  }
                >
                    <div className={classes.toolbar}>
                        <IconButton onClick={()=>props.handleDrawerClose()}>
                            {props.open ?  <ChevronLeftIcon style={{color:'white'}} />:<ChevronRightIcon style={{color:'white'}}/>}
                        </IconButton>
                   </div>
                  <Divider style={{background:'white'}} />
                    <Menu
                      menuList={window.MockData.MenuList}
                      id={props.id}
                      layout="aside"
                      open={props.open}
                      local={props.locate}
                      getPath={(path)=>{
                          if(path.path){
                            props.history.push(path.path);
                          }
                       
                      }}
                    />
                </Drawer>
        )
    }else if(props.layout==="header"){
        return(
            <Menu
            menuList={window.MockData.MenuList}
            id={props.id}
            open={false}
            placement="header"
            local={props.locate}
            getPath={(path)=>{
                if(path.path){
                  props.history.push(path.path);
                }
             
            }}
          /> 
        ) 
    }else{

        return null;
    }  

   
}

export default MenuLayout;