import React from 'react';
import * as axios from 'axios';
import Dialog from '../dialog/index';
import Close from '@material-ui/icons/Close';
import IconButton from '@material-ui/core/IconButton';

const  pdfLib = require('pdfjs-dist');
if ( typeof window !== 'undefined' && 'Worker' in window ) {
    pdfLib.GlobalWorkerOptions.workerSrc  = '//cdn.jsdelivr.net/npm/pdfjs-dist@2.5.207/build/pdf.worker.min.js';
}
const CanvasPdf=({page,renderer,close,open})=>{
    const ref=React.useRef(null);
    const render=(page,render)=>{
        return render.getPage(page).then((pdfPage)=>{
            let viewport =pdfPage.getViewport({scale:1.0});
            ref.current.width=viewport.width;
            ref.current.height=viewport.height;
            let ctx= ref.current.getContext("2d");
            let load=pdfPage.render({
                canvasContext:ctx,
                viewport:viewport
            });
           return load.promise;
         })
     }
     React.useEffect(()=>{
         if(renderer!=null&&page!==0){
             if(open){
                render(page,renderer);
             }
          
         }
         
     },[page])
    return(
        <div style={{background:'rgba(0,0,0,0.5)',textAlign:'center',paddingTop:15}}>
          <IconButton 
              onClick={()=>{
               close();
              }}
              color="primary" 
              style={{
              position:'absolute',
              top:50,
              right:100
          }}><Close/></IconButton>
          <div style={{color:"white",fontSize:"18px"}}>{page}</div>
          <canvas ref={ref}></canvas>
        </div>
    )
}

function Pdf(props){
 React.useEffect(()=>{
     if(props.url){
        http();
     }
   
 },[props.url]);
 const [open,setOpen] =React.useState(false);
 const [fullPageList,setFullPage]=React.useState([]);
 const cache=React.useRef(null);
 const initPdf=(data)=>{
    let load=pdfLib.getDocument(window.URL.createObjectURL(data));
    load.promise.then((res)=>{
     cache.current=res;
     setFullPage([...Array(res._pdfInfo.numPages)].fill(true));
     return 
     
    })
 }
 const http=()=>{
    if(props.url){
        const url=props.url;
        //console.log(props.url);
      //  axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded';
        let  xhr=new XMLHttpRequest();
        xhr.open("GET",props.url,true);
        xhr.responseType = "blob";
        xhr.onreadystatechange = function() {//服务器返回值的处理函数，此处使用匿名函数进行实现
            if (xhr.readyState == 4 && xhr.status == 200) {//
                initPdf(xhr.response);
                setOpen(true);
              
            }
        }
        xhr.send();
        
        // axios.get({
        //     method:'get',
          
        //     responseType:'blob',
        //     headers:{
        //    'Access-Control-Allow-Origin':'*',
        //    'Content-Type':'text/plain; charset=x-user-defined'
        // },
        // }).then((res)=>{
        //     initPdf(res.data);
        //     setOpen(true);
        // })
       }
 }
return (
      <Dialog open={open} fullScreen={true} >
          {fullPageList.length!==0?fullPageList.map((item,index)=>{
              return (
                  <CanvasPdf open={open} page={index+1} key={`${index}`} close={()=>{
                    setOpen(false);
                }} renderer={cache.current}/>
              )
          }):null}
      </Dialog>
      )
}
export default Pdf