import React from 'react';
import { Dialog as MuiDialog, Portal, DialogTitle, DialogContent, DialogActions, Paper } from '@material-ui/core'
import Draggable from 'react-draggable';
import {uuid} from '../../utils/utils'

function DragComponent(props) {
    const { drag, id, ...reset } = props;
    console.log(id);
  
    if (drag) {
        return (
            <Draggable
                handle={`#${id}`}
                onStart={(id)=>{

                    console.log(document.getElementById(id));
                }}
                cancel={'[class*="MuiDialogContent-root"]'}
                >
                <Paper {...reset} />
            </Draggable>
        )
    } else {
        return (
            <Paper {...reset}></Paper>
        )
    }
}
function Dialog(props) {
    const id = "move";
    const { title, actions, drag, open,onClose } = props;
    const [show, setShow] = React.useState(false);
    React.useEffect(() => {
        setShow(open);
    }, [open]);
    const close=()=>{
        setShow(false);
        if(onClose){
            onClose(false)
        }
    }
    return (
        <div>
            <MuiDialog
                PaperComponent={(props) => <DragComponent id={id} drag={drag} {...props}></DragComponent>}
                open={show}
                fullScreen={props.fullScreen}
                onClose={()=>{
                    close();
                }}
            >
                {title ?
                    <DialogTitle style={{ cursor: drag ? 'move' : null }} id={id}>{title}</DialogTitle> : null
                }
                {
                    props.children ?
                        <DialogContent>
                            {props.children}
                        </DialogContent> : null
                }
                {actions ?
                    <DialogActions>
                        {actions}
                    </DialogActions> : null

                }

            </MuiDialog>
        </div>
    )
}
export default Dialog;