import * as types from './configureType'
const initState={
    pc:{
        menuLayout:"aside",
        menuLayoutList:["aisde","header"]
    }
}
export default function(state=initState,action){
    switch(action.type){
        case types.CHANGE_PC_LAYOUT:{
            state.pc.menuLayout=action.params;
            return {
                ...state
            }
        }
        default:
            return {
                 ...state
            }
    }
}