import React from 'react';
import Table from '../../../component/table/index';
import {Box, Grid} from '@material-ui/core';



function User(){
   
return (
      <Box>
        <Grid>
            <Grid></Grid>
            <Grid>
                <Table
                rowDrap={true}
                 columns={window.MockData.table.columns} 
                 data={window.MockData.table.data(10)}>

                </Table>
            </Grid>
        </Grid>
      </Box>
      )
}
export default User