import React from 'react';
import {Grid} from '@material-ui/core'

function Header(props){
    return (
        <Grid container direction="row" justify={props.left&&props.right?'space-between':!props.right?'flex-start':'flex-end'}>
            <HeaderContent {...props}></HeaderContent>
            {props.children?props.children:null}
        </Grid>
    )
}

function HeaderContent(props){
    if(props.left){
        return (
            <Grid item>
                 {props.left}
            </Grid>
        )
    }else if(props.right){
        return (
            <Grid item>
                 {props.right}
            </Grid>
        )
    }else{
        return null
    }
}
export default Header;