import React from 'react';
import mainStyle from '../../styles/index'
import CssBaseline from '@material-ui/core/CssBaseline';
import clsx from 'clsx';
import { IconButton, Select, MenuItem } from '@material-ui/core';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import MenuIcon from '@material-ui/icons/Menu';
import Search from '@material-ui/icons/Search';
import { Route, withRouter, Switch } from 'react-router-dom';
import router from '../../router';
import { connect } from 'react-redux';
import * as localActions from '../../service/locale/action';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Header from './header';
import Popover from '../../component/popper/index';
import { Input } from '@material-ui/core'
import MenuLayout from './MenuLayout';

function Main(props) {
    console.log(props,props.config.pc.menuLayout);
    const classes = mainStyle.mainStyle();
    const [open, setOpen] = React.useState(true);
    const [id, setId] = React.useState(null);
    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(!open);
    };
    const List = [
        { label: props.locate[props.locate.locale].main.account, value: 0 },
        { label: props.locate[props.locate.locale].main.out, value: 1 }
    ];
    return (
        <div className={classes.root}>
            <CssBaseline></CssBaseline>
            <AppBar
                position='fixed'
                className={props.config.pc.menuLayout==="aside"?
                  clsx(classes.appBar,{
                      [classes.appBarShift]:open
                  }):null
              }
            >
                <Toolbar>
                    <IconButton
                        color='inherit'
                        onClick={() => handleDrawerOpen()}
                        style={{ marginLeft: 67 }}
                        edge="start"
                        className={
                            clsx(
                                classes.menuButton, {
                                [classes.hide]: open
                            }
                            )
                        }
                    >
                        <MenuIcon />
                    </IconButton>
                    <Header
                        right={
                            <>
                                <Input
                                    disableUnderline={true}
                                    startAdornment={<Search></Search>}
                                    style={{
                                        background: 'white',
                                        borderRadius: 15,
                                        marginRight: 15,
                                        textIndent: 15,
                                        position: 'relative',
                                        top: 8
                                    }}
                                />

                            </>
                        }
                    >
                        {props.config.pc.menuLayout!=="aside"?
                        <MenuLayout
                            {...props}
                            id={id}
                            layout={props.config.pc.menuLayout}
                            open={open}
                        ></MenuLayout>:null}
                        <Select style={{ color: "white" }}
                            className={classes.outlined}
                            variant="standard"
                            defaultValue="en"
                            classes={{
                                icon: classes.icon,
                            }}
                            onChange={
                                (e) => {
                                    props.changeLocation({ locale: e.target.value });
                                }
                            }>
                            <MenuItem value="ch">中文</MenuItem>
                            <MenuItem value="en">English</MenuItem>
                        </Select>
                        <IconButton color="inherit" >
                            <Popover hover options={List} open={true} selectItem={(item) => {
                                if (item.value === 1) {
                                    props.history.push("/user/login");
                                }
                            }}>
                                <AccountCircle fontSize="large" />
                            </Popover>

                        </IconButton>
                    </Header>
                </Toolbar>
            </AppBar>
            {props.config.pc.menuLayout==="aside"?
            <MenuLayout
                {...props} id={id}
                open={open}
                layout={props.config.pc.menuLayout}
                handleDrawerClose={() => handleDrawerClose()}></MenuLayout>:null
                        }
            <main className={classes.content}>
                <div className={classes.toolbar} />
                <Switch>
                    {router.pc.main.map((item, index) => {
                        return (
                            <Route exact path={item.path} key={`${index}`} component={item.component} />
                        )
                    })}
                </Switch>
            </main>
        </div>
    )
}
export default connect((state) => ({ ...state }), { ...localActions })(withRouter(Main));